<template>
 <div id="app">

  <GalaxyComponent 
  :assets="BGassets"
  :resizeObj="resizeObj" 
  v-if="showGalaxy && BGassets"
  @showGalaxy="handleshowGalaxy"/>

  <!-- <video autoplay loop muted v-else>
    <source src="./assets/Background Loop.mp4">
  </video> -->

  <MainMenu/>
  <audio autoplay loop>
    <source src="./assets/bg audio.mp3" type="audio/ogg">
    <source src="./assets/bg audio.mp3" type="audio/mpeg">
  </audio>
 </div>
</template>

<script>
import GalaxyComponent from './components/GalaxyComponent.vue';
import MainMenu from './components/MainMenu.vue';
import prismic from "./prismic.js";

export default {
  name: 'App',
  components: {
    GalaxyComponent,
    MainMenu
  },
  props: {
    resizeObj:{
      type: Event,
      default: () => {}
    }
  },
  data() {
    return {
      showGalaxy: true,
      link: null,
      BGassets: null
    }
  },
  methods: {
    handleshowGalaxy(e) {
      this.showGalaxy = e;
    }
  },
  beforeMount() {
    // in case someone tries accessing via link and they are offline
    if (!window.navigator.onLine) window.location.href = "./index.html";
    prismic.getData().then((assets) => {
      this.BGassets = assets;
    })

  },
  mounted() {
  }
}
</script>

<style lang="scss">
#app {
  background: black;
}
h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
</style>
