<template>
  <div class="model" ref="model"></div>
</template>

<script>
/* eslint-disable no-unused-vars */
import * as THREE from "../../node_modules/three/build/three.module.js";

// let starImg = require("../assets/star.png");

export default {
  name: "ThreeJS",
  watch: {
    resizeObj: {
      handler() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
      },
       deep: true,
       immediate: true
    },
    assets: {
      handler(assets) {
        this.starImg = assets.star_image.url;
        this.tigerImg = assets.tiger_image.url;
      },
      deep:true,
      immediate:true
    }
  },
  props: {
    width: {
      type: Number,
      default: window.innerWidth
    },
    assets: {
      type: Object,
      default: () => {}
    },
    height: {
      type: Number,
      default: window.innerHeight
    },
    zoom: {
      type: Number,
      default: 18
    },
    resizeObj: {
      type: Event,
      default: () => {}
    }
  },
  data() {
    let scene; let camera; let renderer; let mixer; let clock; let texture; let particlesGeometry; let particlesMaterial; let particles;
    return {
      scene,
      camera,
      renderer,
      mixer,
      clock,
      texture,
      particlesGeometry,
      particlesMaterial,
      particles,
      xMove:.0001,
      yMove:.0001,
      starImg: null,
      tigerImg: null,
      cameraZoomLimit: 0,
      activeGeometries: [],
      tiger: null
    };
  },
  methods: {
    init() {
      this.scene = new THREE.Scene();

      this.camera = new THREE.PerspectiveCamera( 1000, this.width / this.height, 0.1, 1000);
      this.camera.position.set(0,0,this.zoom);

      this.clock = new THREE.Clock();

      this.renderer = new THREE.WebGLRenderer({ alpha: false, antialias: true});
      this.renderer.setClearColor (0x000000, 1);
      this.renderer.setPixelRatio( window.devicePixelRatio ); // prevents pixelation
      this.renderer.setSize(this.width, this.height);
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
      this.renderer.toneMappingExposure = 1;
      this.renderer.shadowMap.enabled = true;

      this.$refs.model.appendChild(this.renderer.domElement);

    },
    tigers() {
      
        var geometry = new THREE.BoxGeometry( 18, 20, 1 );
        var material = new THREE.MeshBasicMaterial( { map: new THREE.TextureLoader().load( this.tigerImg ), transparent: true} );

        this.tiger = new THREE.Mesh( geometry, material );
        this.tiger.rotation.z = 3.15;
        
        this.scene.add(this.tiger);
    },

    stars() {
      
      const vertices = [];
      for ( let i = 0; i < 6000; i ++ ) {
        const x = THREE.MathUtils.randFloatSpread( 2000 );
        const y = THREE.MathUtils.randFloatSpread( 2000 );
        const z = THREE.MathUtils.randFloatSpread( 2000 );
        vertices.push( x, y, z );
      }

      const textureLoader = new THREE.TextureLoader();
      textureLoader.setCrossOrigin('anonymous');
      let texture;

        texture = textureLoader.load( this.starImg, 
        // onLoad callback
        undefined,
        // onProgress callback currently not supported
        undefined,
        // onError callback
        () => {
          this.$emit('showGalaxy', false);
          return;
        }
      );

      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set( 1,1 );

      const geometry = new THREE.BufferGeometry();
      geometry.setAttribute( 'position', new THREE.Float32BufferAttribute( vertices, 3 ) );
      let particlesMaterial = new THREE.PointsMaterial({
        size: 8,
        transparent: true,
        depthWrite: false,
        color: 0xB8A230,
        map: texture
      });
      let particles = new THREE.Points(geometry, particlesMaterial);

      particles.position.z = this.cameraZoomLimit;

      this.activeGeometries.push(particles.uuid);

      this.scene.add(particles);
    },

    remove() {
        let disposableObj = this.scene.getObjectByProperty(this.activeGeometries[0]);
        this.scene.remove(disposableObj);
        this.activeGeometries.pop();
    },

    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
      let delta = this.clock.getDelta();
      this.camera.rotation.y -= this.xMove;
      this.camera.rotation.x -= this.yMove;
      this.camera.position.z += .5;
      if (this.tiger) this.tiger.position.z += .5;
      if (this.camera.position.z >= this.cameraZoomLimit) {

        this.remove();

        this.cameraZoomLimit += 1500;

        this.stars();
      }
      // this.camera.position.y = this.camera.position.y - (this.yMove * 500);
      // this.camera.position.x = this.camera.position.x + (this.xMove * 500);

      if (this.mixer) this.mixer.update(delta);
    },

    mousemove(e) {
      this.xMove = -((e.screenX - (window.innerWidth / 2)) / Math.abs(e.screenX - (window.innerWidth / 2)) || 1) / 1500;
      this.yMove = -((e.screenY - (window.innerHeight / 2)) / Math.abs(e.screenY - (window.innerHeight / 2)) || 1) / 1500;
      // this.camera.position.y = e.screenY / 4;
      // this.camera.rotation.x = e.screenY / 1000;
      // this.camera.rotation.y = e.screenX / 1000;
    }
  },
  emits: ["loaded"],
  mounted() {
    this.init();
    this.stars();
    this.tigers();
    this.animate();
    window.addEventListener("mousemove", this.mousemove);
    
    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(window.innerWidth, window.innerHeight);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
canvas {
margin: auto;

}
.model {
  position: fixed;
  left: 0;
  top: 0;
}
</style>
